/* wrapper for card elements section */
.elements {
margin: 0 auto;
}
/* styling for each card element -outer wrap */
.elements__element {  
    position: relative;
    width: 100%;
    list-style-type: none;
    min-width: 280px;
    background-color: #FFFFFF;
    border-radius: 10px;
    overflow: hidden;
}
/* card elements are arranged as a list -ul */
.elements__list {
        display: inline-grid;
        max-width: 880px;
        grid-template-columns: repeat(3, 1fr);
        margin: 50px auto 67px auto;
        grid-gap: 20px 17px;
        padding: 0;
    }
    
    
    @media screen and (max-width: 800px) {
        .elements__list  {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 25px 20px;
        }
      }
    
      @media screen and (max-width: 540px) {
        .elements__list {
            grid-template-columns: 1fr;
            grid-gap: 20px auto;
        }
      }
    
      @media screen and (max-width: 320px) {
        .elements__list  {
            margin: 42px 0 44px 0;
        }
    }
/* outer wrap for card element information */
.elements__info {
    display: flex;
    justify-content: space-between;
    margin: 25px 15px 31px 21px;
}
.elements__text {
    font-size: 24px;
    font-style: normal;
    font-weight: 900;
    line-height: 29px;
    text-align: left;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin: 0;
}
.elements__image {
    width: 100%;
    height: 282px;
    background-size: cover;
    background-position: center;
    cursor: pointer;
}
.elements__trash {
    background-image: url(/static/media/trash.7ee866f1.svg);
    background-color: transparent;
    position: absolute;
    top: 18px;
    right: 15px;
    height: 20px;
    width: 18px;
    padding: 0;
    border: none;
    /* background-size: 100%; */
    cursor: pointer;
    transition: 1s ease-out;
}

.elements__trash:hover {
  cursor: pointer;
  opacity: 0.6;
}

.elements__trash:focus {
    outline: none;
  }

.elements__trash_disabled {
    display: none;
  }
.elements__heart {
  background-image: url(/static/media/heart.dd31dd06.svg);
  background-repeat: no-repeat;
  background-color: transparent;
  height: 19px;
  width: 21px;
  align-self: center;
  padding: 0;
  border: none;
  cursor: pointer;
  /* transition: .06s ease-out; */
}

.elements__heart:hover {
  opacity: 0.5;  
}

.elements__heart:focus {
  outline: none;
}
.elements__heart_active {
  background-image: url(/static/media/heart_active.ef38b085.svg);
  background-repeat: no-repeat;
  background-color: transparent;
  background-size: contain;
  width: 20px;
  height: 20px;
  border: none;
  margin: 0 auto;
}

.elements__heart_active:hover {
  opacity: 0.5;
  transition: ease-in-out 450ms;
  cursor: pointer;
}

