/* container for pop-up window */
.modal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left:0;
    justify-content: center;
    align-items: center;
    display: none;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
    
    /* animation: */
    animation: fadein .5s;
    -webkit-animation: fadein .5s;
    -moz-animation: fadein .5s;
    -o-animation: fadein .5s;
    -ms-animation: fadein .5s;
    }

  @keyframes fadein {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadein {
    0% {opacity:0;}
    100% {opacity:1;}
  }
/* unhides module popup window when opened */
.modal_open {
    /* display: flex; */
    display: inherit;
}
/* style for popup window, dims page when pop-up open */
.modal__container {
  background: #FFFFFF;
  width: 430px;
  padding: 36px;
  border-radius: 10px;
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 .4em .4em rgba(0, 0, 0, 0.25);
}

