  outline: none;
}

/* non-dynamic pseudo-classes for displaying for error in browser : 

.form__input:invalid {
  border-bottom: 1px solid red; 
} 

.form__input:valid {
  border-bottom: 1px solid green; 
}  */

@media screen and (max-width: 1024px) {
  .form__input {
    margin-top: 76px;
  }
}
.form__input_type_error {
    border-bottom-color: red;
  
    /* The bottom border will become red every time
    the input data is invalid */
  }
.form__button {
    width: 100%;
    height: 50px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 17px;
    text-align: center;
    color: #FFFFFF;
    background-color: #000000;
    border: none;
    padding: 0;
    border-radius: 2px;
    cursor: pointer;
    margin-top: 48px;
}

.form__button:hover {
    opacity: 0.8;
}
.form__button_disabled {
    color: rgba(0, 0, 0, 0.2);
    background-color: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.2);
    cursor: default;
    pointer-events: none;
} 
.form__error_visible {
    visibility: hidden;
    display: none;
    color: #FF0000;
    font-size: 12px;
    line-height: 14.52px;
    font-weight: 400;
    margin-top: 5px;
}
.form__error {
    visibility: visible;
    display: block;
}
